main {
  place-items: center;
  height: 100dvh;
}

.backdrop {
  height: auto;
  padding: 30px 70px;
  backdrop-filter: blur(16px) saturate(180%);
  -webkit-backdrop-filter: blur(16px) saturate(180%);
  background-color: rgba(130 130 130 / 30%);
  border-radius: 12px;
  border: 1px solid rgba(130 130 130 / 10%);
  place-items: center;
  box-shadow: 5px 5px 16px 0 #2657eb33;
}

.backdrop h1 {
  color: #2f2f2f;
  margin-bottom: 16px;
}

.backdrop .input {
  background-color: #fff;
  height: 46px;
  width: auto;
  border-radius: 6px;
}

.backdrop input[type=text] {
  width: 280px;
  height: 46px;
  border-radius: 6px;
  margin-bottom: 16px;
  padding-left: 16px;
  letter-spacing: 0.2px;
  padding-bottom: 1px;
}

.btn {
  height: 40px;
  border-radius: 6px;
  margin: 3px 3px 3px 16px;
  cursor: pointer;
  font-weight: 600;
  font-family: inherit;
  padding:0 14px 1px 14px;
  letter-spacing: 0.2px;
  background-color: #5146b4;
  color: #fff;
}

.btn:hover {
  background-color:rgba(81, 70, 180, 0.9);
}

